<template>
    <div v-if="ready">
        <div class="row">             
            <div class="col-12">
                <h1>{{ strings.contactUsLabel }}</h1> 
            </div>
            <div class="col-6">

                <div>
                    <h5>{{ strings.headOfficce }}</h5>
                    <br>{{ strings.line1 }}
                    <br>{{ strings.line2 }}
                    <br>{{ strings.line3 }}
                    <br>{{ strings.line4 }}
                </div>
            </div>
            <div class="col-6">
                <div>
                    <h5>{{ strings.contactsLabel }}</h5>
                    <br>{{ strings.phoneLabel }}: +968 24117332
                    <br>{{ strings.faxLabel }}:       +968 24117331
                    <br>{{ strings.emailLabel }}:    info@shaleem.co 
                </div>
            </div>
            <div class="col-12 col-md-6">
                <div class="py-5">
                    <h4>{{ strings.jobSeekers.title }} </h4>
                    <br>{{ strings.jobSeekers.description }}
                    <br>{{ strings.jobSeekers.info }} 
                    <br>{{ strings.jobSeekers.help1 }}  
                    <br>{{ strings.jobSeekers.help2 }}  
                    <br>{{ strings.jobSeekers.help3 }}  
                    <br>{{ strings.jobSeekers.help4 }}  
                    <br>{{ strings.jobSeekers.help5 }}  
                </div>
            </div>
            <div class="col-12 col-md-6">
                <div class="py-5">
                    <h4>{{ strings.forSuppliers.title }}</h4>
                    <br>{{ strings.forSuppliers.description }}
                    <br>{{ strings.forSuppliers.help1 }}
                    <br>{{ strings.forSuppliers.help2 }}
                    <br>{{ strings.forSuppliers.help3 }}
                </div>
            </div>
        </div>
        <div class="row justify-content-center border-top">
            <div class="col-md-6 p-3">
                <div>{{ strings.form.name }}</div>
                <Input type="text" />
            </div>
        </div>
        <div class="row justify-content-center">
            <div class="col-md-6 p-3">
                <div>{{ strings.form.email }}</div>
                <Input type="text" />
            </div>
        </div>
        <div class="row justify-content-center">
            <div class="col-md-6 p-3">
                <div>{{ strings.form.subject }}</div>
                <Input type="text" />
            </div>
        </div>
        <div class="row justify-content-center">
            <div class="col-md-6 p-3">
                <div>{{ strings.form.message }}</div>
                <textarea/>
            </div>
        </div>
        <div class="row justify-content-center">
            <div class="col-md-6 p-3">
                <Button type="primary">{{ strings.form.send }}</Button>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
export default {
  name: 'contact',
  props: [ 'lang' ],
  data() {
    return{
      url: this.$parent.url,
      ready: false,
      strings: {}
    }
  },
  watch:{
    lang: function(newLang, oldLang){
      this.setLang();
    }
  },
  created: function(){
    this.setLang();
  },
  methods: {
    setLang : function(){
      axios.get(this.url+'/assets/lang/'+this.lang+'/contact.json')
      .then(r=> {
        this.strings = r.data;
        this.ready = true;
      })
    }
  }
}
</script>

<style lang="scss" scoped>
    input,textarea{
        width: 100%;
    }
    .border-top{
        border-top:7px solid gray;
    }
</style>
